
import { workExp } from "../../utils/data";
import css from "./Work.module.scss";
import { motion } from "framer-motion";
import {
  draw,
  fadeIn,
  slideIn,
  staggerChildren,
  textVariant2,
  zoomIn,
} from "../../utils/motion";
import { BsPatchCheckFill } from "react-icons/bs";

const Work = () => {
  return (
    <motion.section
      variants={staggerChildren}
      initial="hidden"
      whileInView="show"
      viewport={{ once: false, amount: 0.25 }}
      className={`paddings ${css.wrapper}`}
    >
      <a className="anchor" id="work"></a>

      <div className={`innerWidth flexCenter ${css.container}`}>
        {/* heading */}
        <span className="primaryText yPaddings">My Skills</span>

        <div className={`flexCenter ${css.experiences}`}>
          {workExp.map((exp, i) => {
            return (
              <motion.div variants={textVariant2} key={i} className={`flexCenter ${css.exp}`}>
                {/* Render your existing experience content here */}
              </motion.div>
            );
          })}

          {/* New section */}
          <motion.div variants={zoomIn(1, 1)} className={css.experience__container}>
            {/* Frontend Development */}
            <div className={css["experience__frontend"]}>
              <h3>Frontend Development</h3>
              <div className={css["experience__content"]}>
                {/* Replace the following with your actual skills and details */}
                <article className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>React</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                </article>

                <article className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>Angular</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                </article>

                <article className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>JavaScript</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                </article>

                <article className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>TypeScript</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                </article>

                <article className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>HTML</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                </article>

                <article className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>CSS</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                </article>
                {/* Add more skills... */}
              </div>
            </div>

            {/* Backend Development */}
            <div className={css["experience__backend"]}>
              <h3>Backend Development</h3>
              <div className={css["experience__content"]}>
                {/* Replace the following with your actual skills and details */}
                <article className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>NodeJS</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                  
                  
                </article>
                <article  className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>SQL</h4>
                    <small className="text-light">Experienced</small>
                  </div>  

                                   
                </article>
                <article  className={css["experience__details"]}>
                  <BsPatchCheckFill className=".experience__details-icon" />
                  <div>
                    <h4>Java</h4>
                    <small className="text-light">Experienced</small>
                  </div>
                  
                  
                </article>
               
                
                {/* Add more skills... */}
              </div>
            </div>
          </motion.div>
        </div>
      </div>
    </motion.section>
  );
};

export default Work;
