
import { motion } from "framer-motion";
import css from "./Portfolio.module.scss";
import { fadeIn, staggerChildren, textVariant, textVariant2 } from "../../utils/motion";
import portfolio1 from '../../assets/portfolio1.jpg'
import shop from '../../assets/shop.png'
import panel from '../../assets/panel1.png'

const projects = [
  {
    title: "Info Panel",
    imageSrc: panel,
    demoLink: "https://mycode014.github.io/react-panel",
  },
  {
    title: "Rent Car",
    imageSrc: portfolio1,
    demoLink: "https://mycode014.github.io/rentcarapp",
  },
  {
    title: "Web Shop",
    imageSrc: shop,
    demoLink: 'https://mycode014.github.io/react-shop',
  },
];

const Portfolio = () => {
  return (
    <motion.section
      variants={staggerChildren}
      initial="hidden"
      whileInView="show"
      viewport={{ once: false, amount: 0.25 }}
      className={`paddings ${css.wrapper}`}
    >
      <a className="anchor" id="portfolio"></a>

      <div className={`innerWidth flexCenter ${css.container}`}>
        <motion.div variants={textVariant(0.4)} className={`flexCenter ${css.heading}`}>
          <div>
            <span className="primaryText">My Latest Works</span>
            <p style={{ marginTop: "10px" }}></p>
          </div>
          <span className="secondaryText"></span>
        </motion.div>

        <div className={`flexCenter ${css.showCase}`}>
          {projects.map((project, index) => (
            <motion.div key={index} variants={fadeIn("up", "tween", 0.5 + 0.2 * index, 0.6)}>
              <img src={project.imageSrc} alt={`project ${index + 1}`} />
              <div className={css.portfolioInfo}>
                <h3>{project.title}</h3>
                <a href={project.demoLink} className={css['btn-primar']} target='_blank'>
                  Visit app
                </a>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </motion.section>
  );
};

export default Portfolio;
